import { DELETE, PATCH, POST } from "../UrlConfig/MethodType";

// for book review api
export const libraryBookReviewListFunction = (builder) => {
  return builder.query({
    query: (data) =>
      `/library/remark/book/${data.bid}/query?page=${data.page}&limit=${data.limit}`,
    serializeQueryArgs: ({ endpointName }) => {
      return endpointName;
    },
    transformResponse: (response, meta, arg) => {
      if (arg?.search)
        return {
          search: true,
          list: response?.stocktake,
        };
      else
        return {
          page: arg?.page,
          list: response?.stocktake,
        };
    },
    merge: (currentCache, incomingData) => {
      if (incomingData?.search) return incomingData;
      else if (incomingData?.page === 1) return incomingData;
      else return void currentCache?.list?.push(...incomingData?.list);
    },

    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};

export const libraryUpdateBookReviewFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/library/remark/book/${data.bid}/query`,
      method: PATCH,
      body: data.reviewData,
    }),
    // invalidatesTags: ["Cash_finance"],
  });
};
export const libraryRemoveBookReviewFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/library/remark/book/${data.bid}/query?sid=${data?.sid}`,
      method: DELETE,
    }),
    // invalidatesTags: ["Cash_finance"],
  });
};
