import { Suspense, lazy, useEffect, useState } from "react";
import {
  assestsFeedUrl,
  assestsFeedbackUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../LibraryManager.module.css";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import QvipleLoader from "../../../Loader/QvipleLoader";
const BookReviewMenu = lazy(() => import("./BookReviewMenu"));

const BookReviewCard = ({ reivew, sid, onRefetch, bid }) => {
  const [rating, setRating] = useState([
    `${assestsFeedbackUrl}/grey-star.svg`,
    `${assestsFeedbackUrl}/grey-star.svg`,
    `${assestsFeedbackUrl}/grey-star.svg`,
    `${assestsFeedbackUrl}/grey-star.svg`,
    `${assestsFeedbackUrl}/grey-star.svg`,
  ]);
  const [open, setOpen] = useState(false);

  useEffect(() => {
    if (reivew?.rating) {
      let inp = [];
      for (let i = 0; i < 5; i++) {
        if (i < reivew?.rating)
          inp.push(`${assestsFeedbackUrl}/yellow-star.svg`);
        else inp.push(`${assestsFeedbackUrl}/grey-star.svg`);
      }
      setRating(() => inp);
    }
  });

  const onToggle = () => {
    setOpen((pre) => !pre);
  };
  return (
    <>
      <div className={style.brc_card}>
        <div className={style.brcc_inner}>
          <img
            src={
              reivew?.student?.studentProfilePhoto
                ? `${imageShowUrl}/${reivew?.student?.studentProfilePhoto}`
                : `${assestsFeedUrl}/feed-user-avatar.svg`
            }
            alt="student profile"
          />
          <div className={style.brcci_text}>
            <h6>{`${reivew?.student?.studentFirstName ?? ""} ${
              reivew?.student?.studentMiddleName ?? ""
            } ${reivew?.student?.studentLastName ?? ""}`}</h6>

            {sid === reivew?.student?._id ? (
              <img
                src={`${assestsUserFinanceUrl}/menu.svg`}
                alt="menu icon"
                onClick={onToggle}
                style={{
                  cursor: "pointer",
                }}
              />
            ) : null}
          </div>
        </div>
        <div className={style.gsf_star_container}>
          {rating?.map((url, index) => (
            <img key={index} src={url} alt="star icon" />
          ))}
        </div>
        <h6>{reivew?.description ?? ""}</h6>
      </div>

      {open && (
        <Suspense fallback={<QvipleLoader />}>
          <BookReviewMenu
            sid={sid}
            onRefetch={onRefetch}
            bid={bid}
            onClose={onToggle}
          />
        </Suspense>
      )}
    </>
  );
};

export default BookReviewCard;
