import { Suspense, lazy, useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../FinanceManager/FinanceManager.module.css";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useLibraryBookReviewList } from "../../../Hooks/ApiHooks/LibraryManager/library-manager-api";
import { useTranslation } from "react-i18next";
import BookReviewCard from "./BookReviewCard";
import CreateButton from "../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../Loader/QvipleLoader";
const AddBookReview = lazy(() => import("./AddBookReview"));

const BookReview = ({ bid, sid }) => {
  const { t } = useTranslation();
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView();
  const [add, setAdd] = useState(false);
  const {
    libraryBookReviewList,
    libraryBookReviewListLoading,
    libraryBookReviewListRefetch,
  } = useLibraryBookReviewList({
    data: {
      bid: bid,
      page: page,
      limit: 10,
    },
    skip: !bid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (libraryBookReviewList?.list?.length) {
      let obj = nextPage(libraryBookReviewList?.list?.length);
      setState(() => obj);
    }
  }, [libraryBookReviewList?.list?.length]);

  const onRefetch = useCallback(() => {
    setPage(() => 1);
    libraryBookReviewListRefetch();
  }, [libraryBookReviewListRefetch]);

  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  return (
    <>
      <BorderBottom />
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>{t("all_book_review")}</h6>
          {sid ? (
            <div className={style.fm_search}>
              <CreateButton
                label="review"
                onAction={onToggle}
                customStyle={{
                  width: "fit-content",
                  margin: "0",
                  padding: "0.4rem 1rem",
                }}
              />
            </div>
          ) : null}
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />

      <UserMemberContentWrapper>
        {libraryBookReviewList?.list?.map((reivew, index) =>
          libraryBookReviewList?.list?.length === index + 1 ? (
            <div key={reivew?._id} ref={ref}>
              <BookReviewCard
                reivew={reivew}
                sid={sid}
                onRefetch={onRefetch}
                bid={bid}
              />
            </div>
          ) : (
            <BookReviewCard
              reivew={reivew}
              key={reivew?._id}
              sid={sid}
              onRefetch={onRefetch}
              bid={bid}
            />
          )
        )}
        {libraryBookReviewListLoading ? (
          <Loading />
        ) : (
          libraryBookReviewList?.list?.length === 0 && (
            <EmptyMessage
              customStyleContainer={{
                marginTop: "1.5rem",
              }}
              title="No book review list."
            />
          )
        )}
      </UserMemberContentWrapper>

      {add && (
        <Suspense fallback={<QvipleLoader />}>
          <AddBookReview
            onClose={onToggle}
            onRefetch={onRefetch}
            bid={bid}
            sid={sid}
          />
        </Suspense>
      )}
    </>
  );
};

export default BookReview;
