import {
  useLibraryBookReviewListQuery,
  useLibraryRemoveBookReviewMutation,
  useLibraryUpdateBookReviewMutation,
} from "../../../Services/LibraryManager/libraryManagerApiDynamic";

export const useLibraryBookReviewList = (args) => {
  const { data, refetch, isFetching } = useLibraryBookReviewListQuery(
    args.data,
    {
      skip: args.skip,
    }
  );
  return {
    libraryBookReviewList: data,
    libraryBookReviewListRefetch: refetch,
    libraryBookReviewListLoading: isFetching,
  };
};
export const useLibraryUpdateBookReview = () => {
  const [libraryUpdateBookReview] = useLibraryUpdateBookReviewMutation();
  return [libraryUpdateBookReview];
};
export const useLibraryRemoveBookReview = () => {
  const [libraryRemoveBookReview] = useLibraryRemoveBookReviewMutation();
  return [libraryRemoveBookReview];
};
